import React from 'react';
import './item.css';
import loc from '../../../static/loc.png';
import st from '../../../static/st.png';
import LiveHelpIcon from '@material-ui/icons/LiveHelp';
import EventAvailableRoundedIcon from '@material-ui/icons/EventAvailableRounded';
import Button from '@material-ui/core/Button';
import { green} from '@material-ui/core/colors';
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import LazyLoad from 'react-lazy-load';
import Divider from '@material-ui/core/Divider';



const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(2),
      padding: theme.spacing(1),
      width : '100%',
      height: 250,
      float : 'right',
    },
  },
  filter : {
    display: 'flex',
    '& > *' : {
      width : '100%',
      height : 400

    },
  },
}));




const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});


 function Item(props) {
  const Items =  props.items;
  const classes = useStyles();
 
 /* const listItems = Items.map((Item) =>
    <li>{Item}</li>
  );*/
  return (
    <div className='itemRoot'>
     <div className={classes.root}>
      <LazyLoad>
    <Paper variant="outlined">
    <div className='Item'>
    	<div className='row'>
         <div className='col-4'>


         <div className='row'>
         <div className='col-9'>
            <div className='row itemImage'> 
          <div className="filler" >
    <LazyLoad height={150} offsetVertical={150}>
     <img src={("https://picsum.photos/seed/"+Items.brand+"/150")} alt=''/>
    </LazyLoad>
     </div>
            </div>
            <div className='row rentbtn'>
           {Items.available ? <ThemeProvider theme={theme}> <Button variant="contained" disableElevation color='primary'><div className='rent'> Rent Now</div></Button> </ThemeProvider>:null}
          </div>
            </div> 


         <div className='col-3'>
         <div className='thumbnailgroup'>

          <div className="fillerThumbnail" >
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.title+"/50")} alt=''/>
    </LazyLoad>
     </div>
        
               <div className="fillerThumbnail" >
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.city+"/50")} alt=''/>
    </LazyLoad>
     </div>

                <div className="fillerThumbnail">
    <LazyLoad height={50} offsetVertical={50}>
     <img src={("https://picsum.photos/seed/"+Items.state+"/50")} alt=''/>
    </LazyLoad>
    </div>
         </div>
         </div>



          </div>
        
          
          


          </div>




         <div className='col-8'>

         <div className='row upper'>
         <div className='col-6'>
      <div className='title'>{Items.title}  </div>
       <div className='brand'> {Items.brand}</div>
       </div>
       <div className='col-6'>
      
      <div className='pricebox'>   <div className="style-1">
  <del>
    <span className="cutOffPrice">₹{Items.cutOffPrice}</span>
  </del>
  <ins>
    <span className="price">₹{Items.price}</span>
  </ins>
</div></div>
       </div>
       </div>

<Divider variant="middle" />


 <div className='row lower'>

 <div className='col-6'>
           <img src={st} alt='' className='st'/>    <div className='descriptionlabel'> {Items.state}</div> 
           <img src={loc} alt='' className='loc'/>   <div className='description'>{Items.city}</div> 
 </div>
 

          <div className='col-6'>
          <div className='btnbox'>
        
         {Items.available ? <div className='available'><EventAvailableRoundedIcon/> Available</div> :<a href='c' className='btn check'><LiveHelpIcon/> Check availablity</a>}
           </div>
       
          </div>
</div>




          </div>

    	</div>

    </div>
    </Paper>
     </LazyLoad>
    </div>
    </div>
  );
}

export default Item;